
import React, { useState } from 'react';
import { X, ChevronRight, ChevronLeft, Check, Upload } from 'lucide-react';

interface SubmissionModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const SubmissionModal: React.FC<SubmissionModalProps> = ({ isOpen, onClose }) => {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({
    name: '',
    tagline: '',
    framework: 'OpenClaw',
    description: '',
    cost: '0.01',
    latency: '500',
    success: '95'
  });

  if (!isOpen) return null;

  const nextStep = () => setStep(s => Math.min(s + 1, 3));
  const prevStep = () => setStep(s => Math.max(s - 1, 1));

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <div className="fixed inset-0 z-[60] flex items-center justify-center p-4">
      <div className="absolute inset-0 bg-black/60 backdrop-blur-sm" onClick={onClose} />
      <div className="relative bg-[#121212] border border-white/10 rounded-2xl w-full max-w-xl shadow-2xl overflow-hidden animate-in fade-in zoom-in duration-200">
        
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-white/5 bg-white/[0.02]">
          <div>
            <h2 className="text-xl font-bold">Submit Your Agent</h2>
            <p className="text-sm text-white/40">Step {step} of 3</p>
          </div>
          <button onClick={onClose} className="text-white/40 hover:text-white transition-colors">
            <X className="w-6 h-6" />
          </button>
        </div>

        {/* Content */}
        <div className="p-8 min-h-[400px]">
          {step === 1 && (
            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium mb-2 text-white/80">Agent Name</label>
                <input 
                  type="text" 
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  placeholder="e.g. CodeClaw Executor" 
                  className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-2 text-white/80">Tagline (Short Summary)</label>
                <input 
                  type="text" 
                  name="tagline"
                  value={formData.tagline}
                  onChange={handleChange}
                  placeholder="The agent that ships PRs on autopilot." 
                  className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-2 text-white/80">Framework</label>
                <select 
                  name="framework"
                  value={formData.framework}
                  onChange={handleChange}
                  className="w-full bg-[#1A1A1A] border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                >
                  <option>OpenClaw</option>
                  <option>CrewAI</option>
                  <option>LangChain</option>
                  <option>AutoGPT</option>
                  <option>Custom</option>
                </select>
              </div>
            </div>
          )}

          {step === 2 && (
            <div className="space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-2 text-white/80">Avg. Success Rate (%)</label>
                  <input 
                    type="number" 
                    name="success"
                    value={formData.success}
                    onChange={handleChange}
                    className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium mb-2 text-white/80">Avg. Latency (ms)</label>
                  <input 
                    type="number" 
                    name="latency"
                    value={formData.latency}
                    onChange={handleChange}
                    className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                  />
                </div>
              </div>
              <div>
                <label className="block text-sm font-medium mb-2 text-white/80">Cost per Successful Task (USD)</label>
                <input 
                  type="text" 
                  name="cost"
                  value={formData.cost}
                  onChange={handleChange}
                  placeholder="0.01" 
                  className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all"
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-2 text-white/80">Detailed Description</label>
                <textarea 
                  name="description"
                  value={formData.description}
                  onChange={handleChange}
                  rows={4}
                  className="w-full bg-white/5 border border-white/10 rounded-lg px-4 py-2.5 focus:ring-1 focus:ring-blue-500 outline-none transition-all resize-none"
                />
              </div>
            </div>
          )}

          {step === 3 && (
            <div className="space-y-6">
              <div className="p-8 border-2 border-dashed border-white/10 rounded-xl flex flex-col items-center justify-center text-center group hover:border-blue-500/50 transition-all cursor-pointer">
                <div className="w-12 h-12 bg-blue-600/10 rounded-full flex items-center justify-center mb-4 group-hover:scale-110 transition-transform">
                  <Upload className="w-6 h-6 text-blue-500" />
                </div>
                <h4 className="font-bold mb-1">Proof of Work</h4>
                <p className="text-sm text-white/40 mb-4">Upload a technical log (JSON) or a short screen recording of the agent in action.</p>
                <button className="bg-white/5 hover:bg-white/10 text-white px-4 py-2 rounded-lg text-sm transition-all">
                  Browse Files
                </button>
              </div>
              
              <div className="bg-blue-600/10 border border-blue-500/30 rounded-lg p-4 flex items-start gap-3">
                <Check className="w-5 h-5 text-blue-400 mt-0.5" />
                <p className="text-xs text-blue-200/70 leading-relaxed">
                  Agents with verified Proof of Work get featured 4x more often on the homepage and appear in the "Technical Specs" leaderboard.
                </p>
              </div>
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="p-6 border-t border-white/5 flex items-center justify-between">
          <button 
            onClick={prevStep}
            className={`flex items-center gap-2 text-sm text-white/40 hover:text-white transition-colors ${step === 1 ? 'invisible' : ''}`}
          >
            <ChevronLeft className="w-4 h-4" />
            Back
          </button>
          
          {step < 3 ? (
            <button 
              onClick={nextStep}
              className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-lg text-sm font-bold transition-all shadow-lg shadow-blue-600/20"
            >
              Next
              <ChevronRight className="w-4 h-4" />
            </button>
          ) : (
            <button 
              onClick={onClose}
              className="bg-green-600 hover:bg-green-700 text-white px-8 py-2 rounded-lg text-sm font-bold transition-all shadow-lg shadow-green-600/20"
            >
              Finish Submission
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default SubmissionModal;
